            <div class="swiper-wrapper" style="transition-duration: 0ms;">
              <div class="swiper-slide history-swiper history-swiper-2021 contain"
                   style="width: 1502px; height: 100vh; background-position: 80% bottom; background-color: black; background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5) 60%, rgba(0, 0, 0, 0) 100%), url(&quot;https://hfai-static.high-flyer.cn/static/ea01d319202ee55b66096d5be5ac723f/3ab09/history_2021.jpg&quot;); opacity: 1; transform: translate3d(0px, 0px, 0px); transition-duration: 0ms;">
                <div class="content-text active"><h2>Fire-Flyer II and
                  Year of Public Welfare</h2>
                  <div class="swiper-no-swiping swiper-no-swiping-2021 en"><p>High-Flyer AI spent one
                    billion Yuan building Fire-Flyer II, an AIHPC with task-level time-sharing
                    scheduling to share the heterogenous computing power. It has the state-of-the-art
                    hardware: accelerator cards, 200Gbps High Bandwidth. Driven by our own software: the
                    high-throughput parallel file system (3FS), the communication framework for
                    distributed training (hfreduce), the collection of highly optimized DL primitives
                    (hfai.nn), and the easy-to-use DL runtime (hfai), Fire-Flyer II is able to reach the
                    limit of its hardware performance.</p>
                    <p>High-Flyer Quant became a director member of Asset Management Association of
                      Ningbo.</p>
                    <p>Fire-Flyer I retired after 18 months' operation.</p>
                    <p>The High-Flyer Public Welfare Working Group was set up in order to contribute to
                      the society in a professional and sustainable way.</p></div>
                </div>
              </div>
              <div class="swiper-slide swiper-slide-prev history-swiper history-swiper-2020 contain"
                   style="width: 1502px; height: 100vh; background-position: center bottom; background-color: black; background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5) 60%, rgba(0, 0, 0, 0) 100%), url(&quot;https://hfai-static.high-flyer.cn/static/e98bb86e74a4a4c0796827728c89ba17/3ab09/history_2020.jpg&quot;); opacity: 1; transform: translate3d(-1502px, 0px, 0px); transition-duration: 0ms;">
                <div class="content-text active"><h2>Fire-Flyer I</h2>
                  <div class="swiper-no-swiping swiper-no-swiping-2020 en"><p>The total investment of
                    Fire-Flyer I was approximately 200 million Yuan. Equipped with 1,100 accelerator
                    cards, it was put into use in the same year, which offered computing power support
                    for AI research.</p>
                    <p>High-Flyer Capital Management (Hong Kong) Limited (High-Flyer Capital) was
                      approved as QFII (Qualified Foreign Institutional Investor) with an aim to
                      attract overseas funds to invest in the A-share market in the long run.</p>
                  </div>
                </div>
              </div>
              <div class="swiper-slide swiper-slide-visible swiper-slide-active history-swiper history-swiper-2019 contain"
                   style="width: 1502px; height: 100vh; background-position: center bottom; background-color: black; background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.4) 60%, rgba(0, 0, 0, 0) 100%), url(&quot;https://hfai-static.high-flyer.cn/static/cbb6819a68b277a468e53e3c20d6280b/e6125/history_2019.jpg&quot;); opacity: 1; transform: translate3d(-3004px, 0px, 0px); transition-duration: 0ms;">
                <div class="content-text active"><h2>Ten-billion Quant</h2>
                  <div class="swiper-no-swiping swiper-no-swiping-2019 en"><p>High-Flyer AI (Hangzhou
                    High-Flyer AI Fundamental Research Co.,Ltd.) was registered, which was dedicated to
                    the research on AI algorithms and its basic applications. The AI software and
                    hardware team developed the High-Flyer Fire-Flyer I AI cluster, which was equipped
                    with 500 GPUs and interconnected by 200Gbps High Bandwidth.</p>
                    <p>High-Flyer Capital Management (Hong Kong) Limited (High-Flyer Capital) was
                      established and obtained the type 9 license in Hong Kong.</p>
                    <p>High-Flyer Quant became one of the ten-billion quants.</p></div>
                </div>
              </div>
              <div class="swiper-slide swiper-slide-next history-swiper history-swiper-2018 contain"
                   style="width: 1502px; height: 100vh; background-position: 89% bottom; background-color: black; background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5) 60%, rgba(0, 0, 0, 0) 100%), url(&quot;https://hfai-static.high-flyer.cn/static/dabe5d10aacb3fedcda2fa5011dc0527/101ab/history_2018.png&quot;); opacity: 0; transform: translate3d(-4506px, 0px, 0px); transition-duration: 0ms;">
                <div class="content-text active"><h2>Won the Golden Bull Fund Award</h2>
                  <div class="swiper-no-swiping swiper-no-swiping-2018 en"><p>The company decided to adopt
                    the AI approach for development. </p>
                    <p>Computing power bottleneck occurs when single-machine training fails to meet the
                      requirements of complex model computing. At the same time, the increasing
                      training demand contradicts with the limited computing resources, thus a
                      large-scale computing power solution is called for.</p>
                    <p>For the first time, High-Flyer won the Golden Bull Fund Award.</p></div>
                </div>
              </div>
              <div class="swiper-slide history-swiper history-swiper-2017 contain"
                   style="width: 1502px; height: 100vh; background-position: center top; background-color: black; background-image: linear-gradient(to right, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.2) 60%, rgba(0, 0, 0, 0) 100%), url(&quot;https://hfai-static.high-flyer.cn/static/ee0e92821674e963f993eeb7c7605a85/9cd88/history_2017.jpg&quot;); opacity: 0; transform: translate3d(-6008px, 0px, 0px); transition-duration: 0ms;">
                <div class="content-text active"><h2>Overall AI Strategy</h2>
                  <div class="swiper-no-swiping swiper-no-swiping-2017 en"><p>Keep expanding the AI
                    algorithm research team and the AI software and hardware R&amp;D team.</p>
                    <p>AI model calculation has been applied to almost all quantitative strategies by
                      the end of 2017.</p></div>
                </div>
              </div>
              <div class="swiper-slide history-swiper history-swiper-2016 contain"
                   style="width: 1502px; height: 100vh; background-position: left bottom; background-color: black; background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5) 60%, rgba(0, 0, 0, 0) 100%), url(&quot;https://hfai-static.high-flyer.cn/static/d06cef1244cdfdc7c062f769f31a2df8/3ab09/history_2016.jpg&quot;); opacity: 0; transform: translate3d(-7510px, 0px, 0px); transition-duration: 0ms;">
                <div class="content-text active"><h2>The First AI Model</h2>
                  <div class="swiper-no-swiping swiper-no-swiping-2016 en"><p>The first stock position
                    generated by the deep learning algorithm model was launched for real-time trading on
                    October 21, 2016, using GPU for calculation. Before that, the algorithms mainly
                    relied on linear models and traditional machine learning algorithms while the model
                    calculation mainly relied on the CPU.</p></div>
                </div>
              </div>
              <div class="swiper-slide history-swiper history-swiper-2015 contain"
                   style="width: 1502px; height: 100vh; background-color: rgb(151, 158, 168); background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0)), url(&quot;https://hfai-static.high-flyer.cn/static/ed03f529d89f43d2f0a840f3ec6dfd53/23f13/history_2015.png&quot;); opacity: 0; transform: translate3d(-9012px, 0px, 0px); transition-duration: 0ms;">
                <div class="content-text active"><h2>Beginning</h2>
                  <div class="swiper-no-swiping swiper-no-swiping-2015 en"><p>High-Flyer Quant was
                    established. It relies on mathematics and AI for quantitative investment. The
                    high-spirited local team, without any overseas hedge fund background, is creative
                    and diligent, and determined to become the world's top quant hedge fund.</p></div>
                </div>
              </div>
              <div class="swiper-slide history-swiper history-swiper-2008-2014 contain"
